
import { defineComponent, ref, watchEffect, toRefs } from "vue";

import SocialMediaCard from "@/components/SocialMediaCard.vue";
import InstagramCard from "@/components/InstagramCard.vue";

import { SOCIAL_MEDIA } from "@/constants/kyc";
import { getInstagramInfo, getInstagramPosts } from "@/apis/kyc";
import { IgInfoResponse, IgPost } from "@/typings/kyc";

const Component = defineComponent({
  name: "SocialMedia",
  components: {
    SocialMediaCard,
    InstagramCard
  },
  props: {
    brand: {
      type: String,
      default: ""
    }
  },
  setup(props) {
    const { brand } = toRefs(props);
    const infoData = ref<IgInfoResponse | null>(null);
    const postsData = ref<IgPost[]>([]);

    watchEffect(async () => {
      if (!brand.value) return;

      const { data: info } = await getInstagramInfo(brand.value);
      const { data: posts } = await getInstagramPosts("official", brand.value);

      infoData.value = info;
      postsData.value = posts.posts;
    });

    return {
      infoData,
      postsData,

      SOCIAL_MEDIA
    };
  }
});

export default Component;
